
.slide-home-enter-active,
.slide-resume-enter-active,
.slide-portfolio-enter-active,
.slide-contact-enter-active,
.slide-feedback-enter-active,
.slide-blog-enter-active {
  transform: scale(1);
  transition: all 1s cubic-bezier(0.800, 0.300, 0.300, 0.800);
}
.slide-home-enter-active {
  transition-delay: .1s;
}
.slide-resume-enter-active {
  transition-delay: .3s;
}
.slide-portfolio-enter-active {
  transition-delay: .5s;
}
.slide-contact-enter-active {
  transition-delay: .7s;
}
.slide-feedback-enter-active {
  transition-delay: .9s;
}
.slide-blog-enter-active {
  transition-delay: 1.1s;
}
.slide-home-enter,
.slide-home-leave-active,
.slide-resume-enter,
.slide-resume-leave-active,
.slide-portfolio-enter,
.slide-portfolio-leave-active,
.slide-contact-enter,
.slide-contact-leave-active,
.slide-feedback-enter,
.slide-feedback-leave-active,
.slide-blog-enter,
.slide-blog-leave-active {
  transform: translateX(10px) rotateY(90deg) rotate(10deg);
  opacity: 0;
}



