
.slide-detail1-enter-active {
  transition: all .3s ease;
  transition-delay: .4s;
}
.slide-detail1-enter,
.slide-detail1-leave-active {
  transform: translateX(-200px);
  opacity: 0;
}
.slide-detail2-enter-active {
  transition: all .5s ease;
  transition-delay: .6s;
}
.slide-detail2-enter,
.slide-detail2-leave-active {
  transform: translateX(100px);
  opacity: 0;
}
.slide-experience-enter-active {
  transition: all .3s ease;
  transition-delay: .8s;
}
.slide-experience-enter,
.slide-experience-leave-active {
  transform: translateY(200px);
  opacity: 0;
}
.slide-professional-enter-active {
  transition: all .4s ease;
  transition-delay: 1.1s;
}
.slide-professional-enter,
.slide-professional-leave-active {
  transform: translateX(200px);
  opacity: 0;
}



